import { Controller } from '@ali/4ever-cangjie';
import { Table } from '@ali/4ever-plugin-table';

export default function isSelectionInColumns(controller: Controller) {
  const { document, selection } = controller.value;
  const { anchor, focus } = selection;
  const anchorParent = document.getFurthsest(anchor.key, Table.isTable) as
    | Table
    | undefined;
  const focusParent = document.getFurthsest(focus.key, Table.isTable) as
    | Table
    | undefined;
  if (
    anchorParent &&
    anchorParent === focusParent &&
    anchorParent.data.sr
  ) {
    return true;
  }
  return false;
}
